<template>
  <div id="register">
  

    <h1>泡泡商城后台管理系统</h1>
    <div class="components-input-demo-presuffix con1">
      <a-input ref="userNameInput" v-model="username" placeholder="用户名">
        <a-icon slot="prefix" type="user" :style="{ fontSize: '14px' }" />
      </a-input>
      <br />
      <br />
      <a-input ref="passwordInput" v-model="password" placeholder="密码" type="password">
        <a-icon type="key" slot="prefix" :style="{ fontSize: '14px' }" spin/>
      </a-input>
      <a-button type="primary" class="btn" @click="registerAction"
        >注册</a-button
      >
    </div>
  </div>
</template>

<script>
import axios from "axios";
import { Input, Icon, Button } from "ant-design-vue";
export default {
  components: {
    [Input.name]: Input,
    [Icon.name]: Icon,
    [Button.name]: Button,
  },
  data() {
    return {
      username: "",
      password: "",
    };
  },
  methods: {
    // 注册
    registerAction() {
      axios
        .post("/pc/api/user/register", {
          username: this.username,
          password: this.password,
          nickname: this.password,
          phonenumber: this.password,
          job: this.password,
          auth: [],
        })
        .then((data) => {
          console.log(data);
          if (data.data.code === 0) {
            //注册成功
            this.$router.push("/login");
          } else {
            //注册失败
            alert(data.data.message);
          }
        })
        .catch((error) => {
          console.log(error);
          alert("注册失败");
        });
    },
  },
};
</script>

